
import {inject, observer} from 'mobx-react';
import React, {Component} from 'react';

import Filter from './filter'
import Footer from '../../components/Footer';
import Navbar from '../../components/Navbar';

 class Artworks extends Component {
  constructor(props) {
    super(props);
    this.state = {
      filterData: [],
      filterPrice:[],
     
      
    
    };
  }

  componentDidMount() {
    let {
      startingStore: { getAllActiveArtworks,getArtists, getSaleArts, getCategories,getPrintSize},
    } = this.props;
    getSaleArts();
    getArtists();
    getAllActiveArtworks()
    getCategories();
    getPrintSize();
    this.selectedCheckboxes = new Set();
  }
  



  render() {
 
    let {
      startingStore: {listOfCategories, listOfPrintSize},
    } = this.props;
   

 let setCategories =(data)=>{
  const selectedIndex = this.state.filterData.indexOf(data);
  let newSelected =[];

if (selectedIndex === -1) {
  newSelected = newSelected.concat(this.state.filterData, data);
} else if (selectedIndex === 0) {
  newSelected = newSelected.concat(this.state.filterData.slice(1));
} else if (selectedIndex === this.state.filterData.length - 1) {
  newSelected = newSelected.concat(this.state.filterData.slice(0, -1));
} else if (selectedIndex > 0) {
  newSelected = newSelected.concat(
    this.state.filterData.slice(0, selectedIndex),
    this.state.filterData.slice(selectedIndex + 1),
  );
}


this.setState({filterData:newSelected})

 }


 

 let setPrice =(data)=>{
    
  const selectedIndex = this.state.filterPrice.indexOf(data);
  let newSelected =[];

if (selectedIndex === -1) {
  newSelected = newSelected.concat(this.state.filterPrice, data);
} else if (selectedIndex === 0) {
  newSelected = newSelected.concat(this.state.filterPrice.slice(1));
} else if (selectedIndex === this.state.filterPrice.length - 1) {
  newSelected = newSelected.concat(this.state.filterPrice.slice(0, -1));
} else if (selectedIndex > 0) {
  newSelected = newSelected.concat(
    this.state.filterPrice.slice(0, selectedIndex),
    this.state.filterPrice.slice(selectedIndex + 1),
  );
}


this.setState({filterPrice:newSelected})

 }



 let setPrintSize =(data)=>{
  const selectedIndex = this.state.filterData.indexOf(data);
  let newSelected =[];

if (selectedIndex === -1) {
  newSelected = newSelected.concat(this.state.filterData, data);
} else if (selectedIndex === 0) {
  newSelected = newSelected.concat(this.state.filterData.slice(1));
} else if (selectedIndex === this.state.filterData.length - 1) {
  newSelected = newSelected.concat(this.state.filterData.slice(0, -1));
} else if (selectedIndex > 0) {
  newSelected = newSelected.concat(
    this.state.filterData.slice(0, selectedIndex),
    this.state.filterData.slice(selectedIndex + 1),
  );
}


this.setState({filterData:newSelected})

 }




 
    return (
      <div className='home'>
        <Navbar />
        <div className='maincon con artworks'>
          <div className='aside'>
            <div className='filter'>
              <div className='filter'>
              
                <form>
                  <h5>Category</h5>
                  <ul>
               
                    {listOfCategories.map((cat) => (
                      <li className='custom-control custom-checkbox'>
                        <input
                          type='checkbox'
                          className='custom-control-input'
                          // checked={this.state.checkAllCat}
                          id={cat.catType}
                          onChange={() => {setCategories(cat.catType)}}
                        />
                        <label className='custom-control-label' htmlFor={cat.catType} >
                          {cat.catType}
                        </label>
                      </li>
                    ))}
                  </ul>

          
                  <h5>Price</h5>
                  <ul>
               
                    <li className='custom-control custom-checkbox'>
                      <input
                        type='checkbox'
                        className='custom-control-input'
                        id='pricerange1'
                        onChange={() => {
                          setPrice(['0', '5000']);
                        }}
                      />
                      <label className='custom-control-label' htmlFor='pricerange1'>
                        PHP0 - PHP5,000
                      </label>
                    </li>
                    <li className='custom-control custom-checkbox'>
                      <input
                        type='checkbox'
                        className='custom-control-input'
                        id='pricerange2'
                        onChange={() => {
                          setPrice(['5000', '10000']);
                        }}
                      />
                      <label className='custom-control-label' htmlFor='pricerange2'>
                        PHP5,000 - PHP10,000
                      </label>
                    </li>
                    <li className='custom-control custom-checkbox'>
                      <input
                        type='checkbox'
                        className='custom-control-input'
                        id='pricerange3'
                        onChange={() => {
                          setPrice(['10000', '20000']);
                        }}
                      />
                      <label className='custom-control-label' htmlFor='pricerange3'>
                        PHP10,000 - PHP20,000
                      </label>
                    </li>
                    <li className='custom-control custom-checkbox'>
                      <input
                        type='checkbox'
                        className='custom-control-input'
                        id='pricerange4'
                        onChange={() => {
                          setPrice(['20000', '30000']);
                        }}
                      />
                      <label className='custom-control-label' htmlFor='pricerange4'>
                        PHP20,000 - PHP30,000
                      </label>
                    </li>
                    <li className='custom-control custom-checkbox'>
                      <input
                        type='checkbox'
                        className='custom-control-input'
                        id='pricerange5'
                        onChange={() => {
                          setPrice(['30000', '40000']);
                        }}
                      />
                      <label className='custom-control-label' htmlFor='pricerange5'>
                        PHP30,000 - PHP40,000
                      </label>
                    </li>
                    <li className='custom-control custom-checkbox'>
                      <input
                        type='checkbox'
                        className='custom-control-input'
                        id='pricerange6'
                        onChange={() => {
                          setPrice(['40000', '50000']);
                        }}
                      />
                      <label className='custom-control-label' htmlFor='pricerange6'>
                        PHP40,000 - PHP50,000
                      </label>
                    </li>
                    <li className='custom-control custom-checkbox'>
                      <input
                        type='checkbox'
                        className='custom-control-input'
                        id='pricerange7'
                        onChange={() => {
                          setPrice(['50000', '1000000']);
                        }}
                      />
                      <label className='custom-control-label' htmlFor='pricerange7'>
                        PHP50,000 and Up
                      </label>
                    </li>
                  </ul>

    
                  <h5>Orientation/Size</h5>
                  <ul>
              
                    {listOfPrintSize.map((size) => (
                      <li className='custom-control custom-checkbox'>
                        <input
                          type='checkbox'
                          className='custom-control-input'
                          id={size.printSize}
                          onChange={() => {
                            setPrintSize(size.printSize);
                          }}
                        />
                        <label className='custom-control-label' htmlFor={size.printSize}>
                          {size.printSize}
                        </label>
                      </li>
                    ))}
                  </ul>
                 
                </form>
              </div>
            </div>
          </div>
          <div className='main'>
            <section className='latest'>
              <h3>All Artworks  </h3>    
              <Filter filData={this.state.filterData} filPrice={this.state.filterPrice.sort(function(a, b){return a-b})}/>
            </section>
   
          </div>


        </div>
        <Footer />
      </div>
    );
  }
}

export default inject('startingStore')(observer(Artworks));
