import React, {Component} from 'react';
import {MDBBtn, MDBIcon, MDBNavLink} from 'mdbreact';
import {BackTop} from 'antd';
import {Link} from 'react-router-dom';
import {toJS} from 'mobx';
import {inject, observer} from 'mobx-react';
import {Pagination} from 'antd'; //new
import AYLogin from "../../../components/AddYoursLogin";
const numEachPage = 10;
class ArtworkFilter extends Component {
  constructor(props) {
    //new
    super(props);
    this.state = {
     
      artworks:[]
    };
  }


  componentDidMount(){
    let{startingStore:
      {
     
      getSaleArts,getLatestArts}}=this.props;
  
    
    getSaleArts()
    getLatestArts()
  }
  render() {

    
    let {
      startingStore: {
        
        listOfLatestArts
    
     
      },
    } = this.props;
 



        let filteredArtwork = listOfLatestArts.filter((art ) => Number(art.artPrice) >= Number(this.props.filData[0]) && Number(art.artPrice) <= Number(this.props.filData[this.props.filData.length-1]) ).map(art =>art)

        



 


    let artworks = () => {


if(this.props.filData[0] !== undefined){


  return toJS(filteredArtwork)
   
   .map((image,i)=>(
      <li key={i}>
               <Link
            to={{
              pathname: `/Art/${image.artworkID}/${image.artistName}`}} className='artlink'>
          <div className='artlabel'>
            <span className='new'>NEW</span>
            <span className='hot'>HOT</span>
            <span className='type'>
              {image.artType === 'Secondary'
                ? 'Second Edition'
                : 'Original'}
            </span>
          </div>
     
            <img src={image.artworkImg} alt='artwork' />
          </Link>
        
        <div className='artistinfo clearfix pad10'>
        
        <Link
            to={{
                    pathname: `/Art/${image.artworkID}/${image.artistName}`,
                    state: {artistName:image.artistName}
             
                }}
              >
                   <p className='artistname'>{image.artName}</p>
               
              </Link>

              
              <br/>
                 
              <Link
            to={{
                    pathname: `/Artist/${image.artistName}`,
                    state: {artistName:image.artistName}
               
                }}
              >
                   <p className='artistname' style={{fontSize:'10px',fontStyle:'italic'}}>by {image.artistName}</p>
               
              </Link>
      
      
          <MDBBtn
                className='ifollow'
                color='transparent'
                
                rounded
                title='Add To My Lists' 
              //  onClick={()=>{addtoList(image)}}
              >
              
                  <MDBIcon icon='plus' />
              
                </MDBBtn>
    </div>
      </li>
 ))
}else{
    return toJS(listOfLatestArts.filter(art=>  
      {
        if(art.artworkStatus === 'Active' ){
          return art
        }
    })).reverse()
    .map((image,i) =>  (
        <li key={i}>
                 <Link
              to={{
                pathname: `/Art/${image.artworkID}/${image.artistName}`}} className='artlink'>
            <div className='artlabel'>
              <span className='new'>NEW</span>
              <span className='hot'>HOT</span>
              <span className='type'>
                {image.artType === 'Secondary'
                  ? 'Second Edition'
                  : 'Original'}
              </span>
            </div>
       
              <img src={image.artworkImg} alt='artwork' />
            </Link>
          
          <div className='artistinfo clearfix pad10'>
          
          <Link
              to={{
                      pathname: `/Art/${image.artworkID}/${image.artistName}`,
                      state: {artistName:image.artistName}
               
                  }}
                >
                     <p className='artistname'>{image.artName}</p>
                 
                </Link>
  
                
                <br/>
                   
                <Link
              to={{
                      pathname: `/Artist/${image.artistName}`,
                      state: {artistName:image.artistName}
                 
                  }}
                >
                     <p className='artistname' style={{fontSize:'10px',fontStyle:'italic'}}>by {image.artistName}</p>
                 
                </Link>
        
        
            <MDBBtn
                  className='ifollow'
                  color='transparent'
                  
                  rounded
                  title='Add To My Lists' 
                //  onClick={()=>{addtoList(image)}}
                >
                
                    <MDBIcon icon='plus' />
                
                  </MDBBtn>
      </div>
        </li>
      )
      )
  }

  }
    return (
      <div>

          <ul className='col3img clearfix'>
            {artworks()}

          </ul>
      

    

        <div>
   
        </div>

        <div className="bttop clearfix">
              <AYLogin login={"ay"} />
              <BackTop style={{ margin: "auto" }}>
                {"     "}
                <a href="#!" style={{ color: "black" }}>
                  Back to Top <MDBIcon icon="caret-right" />{" "}
                </a>{" "}
              </BackTop>
            </div>
      </div>
    );
  }
}

export default inject('startingStore')(observer(ArtworkFilter));
