import { inject, observer } from 'mobx-react';
import React, { Component } from 'react';
import ArtworkFilter from './filter';
import Footer from '../../components/Footer';
import Navbar from '../../components/Navbar';

import {MDBBtn, MDBIcon, MDBNavLink} from 'mdbreact';
import {BackTop} from 'antd';
import {Link} from 'react-router-dom';
import {toJS} from 'mobx';

import {message} from 'antd';

class Artworks extends Component {
  constructor(props) {
    super(props);
    this.state = {
      filterData: [],
    };
  }

  componentDidMount() {
    let {
      startingStore: {getArtists, getLatestArts, getCategories},
    } = this.props;
    getLatestArts();
    getArtists();
    getCategories();

    this.selectedCheckboxes = new Set();
  }



  render() {
   
    let mydata = JSON.parse(sessionStorage.getItem('userData'))
    let {
      startingStore: { mylists, addMyLists,listOfMyLists,},
    } = this.props;

  

  //  let setPrice = (amt) => {
  
   
      
  //     // let filteredArtwork = listOfLatestArts.filter((art ) => parseInt(art.artPrice) >= parseInt(amt[0]) && parseInt(art.artPrice) <= parseInt(amt[1]) ).map(art =>art)
     
  //     this.setState({filterData: arts});
  //   }

    let setPrice =(data)=>{
    
      const selectedIndex = this.state.filterData.indexOf(data);
      let newSelected =[];
    
    if (selectedIndex === -1) {
      newSelected = newSelected.concat(this.state.filterData, data);
    } else if (selectedIndex === 0) {
      newSelected = newSelected.concat(this.state.filterData.slice(1));
    } else if (selectedIndex === this.state.filterData.length - 1) {
      newSelected = newSelected.concat(this.state.filterData.slice(0, -1));
    } else if (selectedIndex > 0) {
      newSelected = newSelected.concat(
        this.state.filterData.slice(0, selectedIndex),
        this.state.filterData.slice(selectedIndex + 1),
      );
    }
    
    
    this.setState({filterData:newSelected})
    
     }
     
     console.log(this.state.filterData.sort(function(a, b){return a-b}),'aaaa')
    
    let addtoList=(rtwrk)=>{

      let getmyList = listOfMyLists.filter( art => art.artworkID === rtwrk.artworkID).length
    
    if (getmyList === 0){
       
       mylists.setProperty('mylistsID',`${rtwrk.artworkID.slice(0,4)}-${Math.floor(1000 + Math.random() * 900)}`)
       mylists.setProperty('accID',mydata.accID)
       mylists.setProperty('artworkID',rtwrk.artworkID)
     
       mylists.setProperty('artName',rtwrk.artName)
       mylists.setProperty('artTheme',rtwrk.artTheme)
       mylists.setProperty('artStyle',rtwrk.artStyle)
       mylists.setProperty('artPrice',rtwrk.artPrice)
       mylists.setProperty('artistID',rtwrk.accID)
       mylists.setProperty('artistName',rtwrk.artistName)
       mylists.setProperty('artworkDateCreated',rtwrk.artworkDateCreated)
       mylists.setProperty('artType',rtwrk.artType)
       mylists.setProperty('artworkImg',rtwrk.artworkImg)
       
       addMyLists()
    
       const success = () => {
        message
          .loading('', 0.5)
          .then(() => message.success('Artwork added to your list', 3));
      };
      setTimeout(() =>{
        success()
      },500)
    }else{
      const success = () => {
        message
          .loading('', 0.5)
          .then(() => message.success('Artwork already on your list', 3));
      };
      setTimeout(() =>{
        success()
      },500)
     
    }
     }
    return (
      <div className='home'>
        <Navbar />
        <div className='maincon con artworks'>
          <div className='aside'>
            <div className='filter'>
              <div className='filter'>
               
                <form>
         

                  <h5>Price</h5>
                  <ul>
              
                    <li className='custom-control custom-checkbox'>
                      <input
                        type='checkbox'
                        className='custom-control-input'
                        id='pricerange1'
                        onChange={() => {
                          setPrice(['0', '5000']);
                        }}
                      />
                      <label className='custom-control-label' htmlFor='pricerange1'>
                        PHP0 - PHP5,000
                      </label>
                    </li>
                    <li className='custom-control custom-checkbox'>
                      <input
                        type='checkbox'
                        className='custom-control-input'
                        id='pricerange2'
                        onChange={() => {
                          setPrice(['5000', '10000']);
                        }}
                      />
                      <label className='custom-control-label' htmlFor='pricerange2'>
                        PHP5,000 - PHP10,000
                      </label>
                    </li>
                    <li className='custom-control custom-checkbox'>
                      <input
                        type='checkbox'
                        className='custom-control-input'
                        id='pricerange3'
                        onChange={() => {
                          setPrice(['10000', '20000']);
                        }}
                      />
                      <label className='custom-control-label' htmlFor='pricerange3'>
                        PHP10,000 - PHP20,000
                      </label>
                    </li>
                    <li className='custom-control custom-checkbox'>
                      <input
                        type='checkbox'
                        className='custom-control-input'
                        id='pricerange4'
                        onChange={() => {
                          setPrice(['20000', '30000']);
                        }}
                      />
                      <label className='custom-control-label' htmlFor='pricerange4'>
                        PHP20,000 - PHP30,000
                      </label>
                    </li>
                    <li className='custom-control custom-checkbox'>
                      <input
                        type='checkbox'
                        className='custom-control-input'
                        id='pricerange5'
                        onChange={() => {
                          setPrice(['30000', '40000']);
                        }}
                      />
                      <label className='custom-control-label' htmlFor='pricerange5'>
                        PHP30,000 - PHP40,000
                      </label>
                    </li>
                    <li className='custom-control custom-checkbox'>
                      <input
                        type='checkbox'
                        className='custom-control-input'
                        id='pricerange6'
                        onChange={() => {
                          setPrice(['40000', '50000']);
                        }}
                      />
                      <label className='custom-control-label' htmlFor='pricerange6'>
                        PHP40,000 - PHP50,000
                      </label>
                    </li>
                    <li className='custom-control custom-checkbox'>
                      <input
                        type='checkbox'
                        className='custom-control-input'
                        id='pricerange7'
                        onChange={() => {
                          setPrice(['50000', '1000000']);
                        }}
                      />
                      <label className='custom-control-label' htmlFor='pricerange7'>
                        PHP50,000 and Up
                      </label>
                    </li>
                  </ul>
         
              
                </form>
              </div>
            </div>
          </div>
          <div className='main'>
            <section className='latest'>
              <ArtworkFilter filData={this.state.filterData.sort(function(a, b){return a-b})}/>
                        
            </section>


     

         
          </div>


        </div>
        <Footer />
      </div>
    );
  }
}

export default inject('startingStore')(observer(Artworks));
