/* eslint-disable jsx-a11y/anchor-is-valid */
import { message, Skeleton } from "antd";
import { MDBBtn, MDBIcon, MDBLink, MDBNavLink } from "mdbreact";
import { inject, observer } from "mobx-react";
import moment from "moment";
import React, { Component, Fragment } from "react";
import { Link, withRouter } from "react-router-dom";
import {
  EmailIcon,
  EmailShareButton,
  FacebookIcon,
  FacebookShareButton,
} from "react-share";
import AddToBag from "../../components/AddToBag";
import Footer from "../../components/Footer";
import Navbar from "../../components/Navbar";
import ViewRoom from "../../components/ViewRoom";

import ATLogin from "./../../components/ATLogin";
import LoginCart from "./../../components/CartLogin/";
import FollowLogin from "./../../components/FollowLogin";

class SingleArt extends Component {
  constructor(props) {
    super(props);
    this.state = {
      img: [],
      id: "",
      artistImg: "",
      artistInfoList: [],
      artistData: [],
      birthYear: "",
      accDescription: "",
      accFollowers: "",
      artworkInfo: [],
      artworkList: [],
      artworkName: "",
      isMounted: false,
      condition: false,
      isToggleOn: true,
      selectedSize: "AP - portrait, 1.5x2",
      height: "2cm",
      width: "1.5cm",
      framingOption: false,
      artworkInfo:[],

    
    };

    this.handleClick = this.handleClick.bind(this);
  }


  handleClick() {
    this.setState(function (prevState) {
      return {
        isToggleOn: !prevState.isToggleOn,
        condition: !this.state.condition,
      };
    });
  }

  componentDidMount() {
    window.scrollTo(0, 0)


    if (this.props.location.state){
      this.setState({
        artworkInfo: this.props.location.state.artID
      });
}

   
    let {
      startingStore: {
        getArtistArtwork,
        getSingleArtists,
        getSingleArtworkInfo,
        getToCart,
        getPrintSize,
        getPrintOption,
        getAllArtworks,
      },
    } = this.props;
    getSingleArtworkInfo(this.props.match.params.id);
    getSingleArtists(this.props.match.params.name);
    getArtistArtwork(this.props.match.params.name);
    getToCart();
    getPrintSize();
    getPrintOption();
    getAllArtworks();

  }

  checkFollower = (follower) => {
    if (follower !== undefined && follower[0] !== "") {
      return follower.length;
    } else {
      return 0;
    }
  };

  followArtist = () => {
    message.loading({ content: "Loading..." });
    setTimeout(() => {
      message.success({ content: "Loaded!", duration: 2 });
    }, 1000);
  };

  render() {
    const getLastItem = thePath => thePath.substring(thePath.lastIndexOf('/') + 1)
   
    let logged = JSON.parse(sessionStorage.getItem("userData"));
    let {
      startingStore: {
        cart,
        listOfSingleArtwork,
        listofArtistInfo,
        listofArtistArtwork,
        followArtist,
        listRelatedWorkByCategory,
        listOfMyLists,
        listOfPrintSize,
        addMyLists,
        mylists,
        addArtShare,
        artshare,
        listOfPrintoption,
        listOfArtworks,
      },
    } = this.props;


   let artworkData = listOfArtworks.filter(data => data.artworkID == decodeURI(window.location.href).split('/').slice(-2)[0])

  
 

    let changeMaterial = (data) => {
      if (
        data === "Print on Sintra (Matte)" ||
        data === "Print on Sintra (Glossy)"
      ) {
        this.setState({
          framingOption: true,
        });
      } else {
        this.setState({
          framingOption: false,
        });
      }

      cart.setProperty("artworkMaterial", data);
    };

    const options = [];
    for (let i = 1; i <= 50; i++) {
      options.push(i);
    }

    let mydata = JSON.parse(sessionStorage.getItem("userData"));

    let changeSize = (size) => {
      cart.setProperty("artworkSize", size);

      if (size === "AP - portrait, 1.5x2") {
        this.setState({
          height: "2cm",
          width: "1.5cm",
          selectedSize: "AP - portrait, 1.5x2",
        });
      } else if (size === "AP - portrait, 3x4") {
        this.setState({
          height: "4cm",
          width: "3cm",
          selectedSize: "AP - portrait, 3x4",
        });
      } else if (size === "B - square, 2x2") {
        this.setState({
          height: "2cm",
          width: "2cm",
          selectedSize: "B - square, 2x2",
        });
      } else if (size === "B - square, 4x4") {
        this.setState({
          height: "4cm",
          width: "4cm",
          selectedSize: "B - square, 4x4",
        });
      } else if (size === "CP - portrait, 2x3") {
        this.setState({
          height: "3cm",
          width: "2cm",
          selectedSize: "CP - portrait, 2x3",
        });
      } else if (size === "CP - portrait, 4x6") {
        this.setState({
          height: "6cm",
          width: "4cm",
          selectedSize: "CP - portrait, 4x6",
        });
      } else if (size === "DL - landscape, 2x1.5") {
        this.setState({
          height: "1.5cm",
          width: "2cm",
          selectedSize: "DL - landscape, 2x1.5",
        });
      } else if (size === "DL - landscape, 4x3") {
        this.setState({
          height: "3cm",
          width: "4cm",
          selectedSize: "DL - landscape, 4x3",
        });
      } else if (size === "EL - landscape, 3x2") {
        this.setState({
          height: "2cm",
          width: "3cm",
          selectedSize: "EL - landscape, 3x2",
        });
      } else if (size === "EL - landscape, 6x4") {
        this.setState({
          height: "4cm",
          width: "6cm",
          selectedSize: "EL - landscape, 6x4",
        });
      }
    };

    let addtoList = (rtwrk) => {
      let getmyList = listOfMyLists.filter(
        (art) => art.artworkID === rtwrk.artworkID
      ).length;

      if (getmyList === 0) {
        mylists.setProperty(
          "mylistsID",
          `${rtwrk.artworkID.slice(0, 4)}-${Math.floor(
            1000 + Math.random() * 900
          )}`
        );
        mylists.setProperty("accID", logged.accID);
        mylists.setProperty("artworkID", rtwrk.artworkID);

        mylists.setProperty("artName", rtwrk.artName);
        mylists.setProperty("artTheme", rtwrk.artTheme);
        mylists.setProperty("artStyle", rtwrk.artStyle);
        mylists.setProperty("artPrice", rtwrk.artPrice);
        mylists.setProperty("artistID", rtwrk.accID);
        mylists.setProperty("artistName", rtwrk.artistName);
        mylists.setProperty("artworkDateCreated", rtwrk.artworkDateCreated);
        mylists.setProperty("artType", rtwrk.artType);
        mylists.setProperty("artworkImg", rtwrk.artworkImg);

        addMyLists();

        const success = () => {
          message
            .loading("", 0.5)
            .then(() => message.success("Artwork added to your list", 3));
        };
        setTimeout(() => {
          success();
        }, 500);
      } else if (getmyList === 1) {
        const success = () => {
          message
            .loading("", 0.5)
            .then(() => message.success("Artwork already on your list", 3));
        };
        setTimeout(() => {
          success();
        }, 500);
      }
    };
    let artworkList = listofArtistArtwork.map((data) => data.artSize);

    let countShares = () => {
      artshare.setProperty(
        "shareID",
        `${logged.accID}-${Math.floor(100 + Math.random() * 900)}`
      );
      artshare.setProperty("userID", logged.accID);

      artshare.setProperty("social", "Facebook");
      artshare.setProperty("dateTime", moment().format("MMM/DD/YYYY"));
      addArtShare();
    };

    return (
      <div className="home">
        <Navbar />
        <div className="maincon con">
          <div className="artcon">
            <div className="clearfix">
              <div className="left">
                <div className="artworkInfo">
                  <h2 className="title artistname">
                    {artworkData[0] !== undefined ? (
                      artworkData[0].artistName
                    ) : (
                      <Fragment>
                  
                      <Skeleton/>
                      </Fragment>
                    )}
                    <MDBBtn
                      color="primary"
                      style={{
                        borderRadius: "5px",
                        width: "50px",
                        height: "25px",
                        fontSize: "8px",
                        margin: 0,
                        padding: "2px",
                        marginLeft: "16px",
                      }}
                      outline
                      title={this.state.isToggleOn ? "Follow" : "Unfollow"}
                      onClick={() =>
                        this.handleClick(followArtist(listofArtistInfo._id))
                      }
                    >
                      {mydata === null ? (
                        <FollowLogin />
                      ) : this.state.isToggleOn === null ? (
                        <div>
                          {" "}
                          <MDBIcon
                            icon="plus"
                            style={{
                              float: "left",
                              fontSize: "9px",
                              color: "#4285F4",
                              marginTop: "2px",
                            }}
                          />
                          <p style={{ fontSize: "9px", color: "#4285F4" }}>
                            Follow
                          </p>
                        </div>
                      ) : (
                        "Following"
                      )}
                    </MDBBtn>
                  </h2>
                  <span className="arttitle">
                    {artworkData[0] !== undefined ? (
                      artworkData[0].artName
                    ) : (
                      <Skeleton active />
                    )}
                    ,&nbsp;
                  </span>
                  <span className="year">
                    {artworkData[0] !== undefined ? (
                      artworkData[0].artworkDateCreated
                    ) : (
                      <Skeleton active />
                    )}
                  </span>
                  <div className="artImg">
                    <img
                      src={
                        artworkData[0] !== undefined ? (
                          artworkData[0].artworkImg
                        ) : (
                          <Skeleton active />
                        )
                      }
                      alt=""
                    />

                    {artworkData[0] !== undefined ? (
                      <ul className="artOpt">
                        {" "}
                        {logged === null ? (
                          <li>
                            <ATLogin />
                          </li>
                        ) : (
                          <li
                            onClick={() => {
                              addtoList(artworkData[0]);
                            }}
                          >
                            <MDBLink
                              to="#"
                              onClick={() => {
                                addtoList(artworkData[0]);
                              }}
                              style={{ marginTop: "-6px" }}
                            >
                              Add to List
                            </MDBLink>
                          </li>
                        )}

                        <li  >
                       
                          <ViewRoom
                       
                            img={artworkData[0].artworkImg}
                            Aheight={this.state.height}
                            Awidth={this.state.width}
                            selectedsize={this.state.selectedSize}
                          />
                        
                        </li>{" "}
                      </ul>
                    ) : (
                      <Skeleton active />
                    )}
                    {logged === null ? (
                      <Fragment></Fragment>
                    ) : (
                      <div style={{ marginTop: "10px" }}>
                        <FacebookShareButton
                          url={window.location.href}
                          appId="216207193165878"
                          pageId="301933277234280"
                          subject="Share"
                          hashtag="#ArtBasically"
                          style={{ marginRight: "8px" }}
                          onShareWindowClose={() => countShares()}
                        >
                          <FacebookIcon size={40} round={true} />
                        </FacebookShareButton>

                        <EmailShareButton url={window.location.href}>
                          <EmailIcon size={40} round={true} />
                        </EmailShareButton>
                      </div>
                    )}
                  </div>
                  <div className="abtArtist">
                    <h4>About the Artist</h4>
                    <span>
                      {artworkData[0] !== undefined ? (
                        getLastItem(decodeURI(window.location.href))
                      ) : (
                        <Skeleton active />
                      )}
                      , b.
                      {listofArtistInfo.birthYear}.
                    </span>
                    <p>{listofArtistInfo.accDescription}</p>

                    
                        <Link
                              to={{
                                pathname: `/Artist/${getLastItem(decodeURI(window.location.href))}`,
                                state: {
                                  artistName: getLastItem(decodeURI(window.location.href)),
                                 
                                },
                              }}
                            >
                      Visit artist profile
                      <MDBIcon icon="caret-right" />
                      </Link>
                  </div>
                </div>
              </div>
              <div className="right">
                <h2>
                  Customize your <br /> order.
                </h2>

                <p
                  className="inlinep"
                  style={{ marginTop: "-10px", paddingBottom: "22px" }}
                >
                  Can't Decide? <MDBNavLink to="#!">We can help.</MDBNavLink>
                </p>
                <form
                  onSubmit={this.submitHandler}
                  className="regform uploadform custform"
                >
                  <div style={{ marginBottom: "16px" }}>
                    <span>Material</span>
                    <select
                      onChange={(artworkMaterial) =>
                        changeMaterial(artworkMaterial.target.value)
                      }
                    >
                      <option selected value="">
                        Choose Material
                      </option>
                      {listOfPrintoption
                        .filter((data) => data.printCat === "material")
                        .map((mat) => {
                          return (
                            <option key={mat.optionID} value={mat.printOptions}>
                              {mat.printOptions}
                            </option>
                          );
                        })}
                    </select>
                    <a className="bluelink" href="">
                      What material is the best for you?
                    </a>
                  </div>

                  <div style={{ marginBottom: "16px" }}>
                    <span>Size</span>

                    <select
                      onChange={(Asize) => changeSize(Asize.target.value)}
                    >
                      {listOfPrintSize.map((sizes) => (
                        <option key={sizes.printSize} value={sizes.printSize}>
                          {sizes.printSize == artworkList[0]
                            ? `${sizes.printSize} Recommended`
                            : sizes.printSize}
                        </option>
                      ))}
                    </select>
                    <a className="bluelink" href="">
                      Which size best fits your needs?
                    </a>
                  </div>

                  <div style={{ marginBottom: "16px" }}>
                    <span>Framing Options</span>
                    <select
                      disabled={this.state.framingOption}
                      onChange={(artworkFramingOptions) =>
                        cart.setProperty(
                          "artworkFramingOptions",
                          artworkFramingOptions.target.value
                        )
                      }
                    >
                      <option selected value="">
                        Choose Framing
                      </option>
                      {listOfPrintoption
                        .filter((data) => data.printCat === "material")
                        .map((mat) => {
                          return (
                            <option key={mat.optionID} value={mat.printOptions}>
                              {mat.printOptions}
                            </option>
                          );
                        })}
                    </select>
                    <a className="bluelink" href="">
                      Which framing option is best for you?
                    </a>
                  </div>
                  <div style={{ marginBottom: "16px" }}>
                    <span>Quantity</span>
                    {listOfSingleArtwork.artType === "Original" ? (
                      <select
                        onChange={(artworkQuantity) =>
                          cart.setProperty(
                            "artworkQuantity",
                            artworkQuantity.target.value
                          )
                        }
                      >
                        <option selected value="">
                          Choose Quantity
                        </option>
                        <option value="1">1</option>
                      </select>
                    ) : (
                      <select
                        onChange={(artworkQuantity) =>
                          cart.setProperty(
                            "artworkQuantity",
                            artworkQuantity.target.value
                          )
                        }
                      >
                        {options.map((option) => (
                          <option key={option} value={option}>
                            {option}
                          </option>
                        ))}
                      </select>
                    )}
                    <a className="blacklink" href="">
                      This is part of a limited edition set of 12.
                    </a>
                  </div>
                  <div>
                    <span>Your total is</span>
                    <span className="nopad">
                      PHP{" "}
                      {Number(
                        parseFloat(
                          artworkData[0] !== undefined
                            ? parseFloat(artworkData[0].artPrice) *
                                parseFloat(cart.artworkQuantity)
                            : "0.00"
                        )
                      ).toLocaleString("en")}
                    </span>
                    <a className="bluelink" href="">
                      How long will your order arrive?
                    </a>
                    <div style={{ float: "right" }}>
                      {mydata === null ? (
                        <LoginCart />
                      ) : (
                        <AddToBag
                          cartData={artworkData[0]}
                          disable={
                            cart.artworkFramingOptions !== "" ? true : false
                          }
                        />
                      )}

                      {/* <ViewCart cartData={listOfSingleArtwork[0]} /> */}
                    </div>
                  </div>
                </form>
              </div>
            </div>
          </div>
          <section className="relcat">
            <h3>
              Other Works by{" "}
              {artworkData[0] !== undefined ? (
                artworkData[0].artistName
              ) : (
                <Skeleton active />
              )}
            </h3>
            <a href="#!" className="seemore">
              See More &nbsp;
              <MDBIcon icon="caret-right" />
            </a>
            <ul className="col3img clearfix">
              {artworkData[0] !== undefined ? (
                listOfArtworks
                  .filter((data) => data.artistName == artworkData[0].artistName && data.artworkStatus == "Active" && data.artworkID !== this.props.match.params.id)
                  .reverse()
                  .slice(0, 6)
                  .map((art) => {
                   
                    
                      return (
                        <li>
                          <a href="" className="artlink">
                            <div className="artlabel">
                              <span className="new">NEW</span>
                              <span className="hot">HOT</span>
                            </div>
                            <Link
                              to={{
                                pathname: `/Art/${art.artworkID}/${art.artistName}`,
                              }}
                            >
                              <img src={art.artworkImg} alt="" />
                            </Link>
                          </a>
                          <div className="artistinfo">
                            <p>
                              {listOfSingleArtwork[0] !== undefined ? (
                                art.artName
                              ) : (
                                <Skeleton active />
                              )}
                            </p>
                          </div>
                        </li>
                      );
                    
                  })
              ) : (
                <Skeleton />
              )}
            </ul>
          </section>
          <section className="relcat">
            <h3>Related Works by Category</h3>
            <a href="#!" className="seemore">
              See More &nbsp;
              <MDBIcon icon="caret-right" />
            </a>
            <ul className="col3img clearfix">
              {listOfArtworks !== undefined ? (
                listOfArtworks
                  .filter((data) => data.artTheme[0] == artworkData[0].artTheme && data.artworkID !== this.props.match.params.id)
                  .reverse()
                  .slice(0, 3)
                  .map((art) => {
                  
                      return (
                        <li>
                          <a href="" className="artlink">
                            <div className="artlabel">
                              <span className="new">NEW</span>
                              <span className="hot">HOT</span>
                            </div>
                            <Link
                              to={{
                                pathname: `/Art/${art.artworkID}/${art.artistName}`,
                              }}
                            >
                              <img src={art.artworkImg} alt="" />
                            </Link>
                          </a>
                          <div className="artistinfo">
                            <p>
                              {listOfSingleArtwork[0] !== undefined ? (
                                art.artName
                              ) : (
                                <Skeleton active />
                              )}
                            </p>
                          </div>
                        </li>
                      );
                    
                  })
              ) : (
                <Skeleton />
              )}
            </ul>
          </section>
         
        </div>
        <Footer />
      </div>
    );
  }
}

export default withRouter(inject("startingStore")(observer(SingleArt)));
