import React, {Component} from 'react';
import {MDBBtn, MDBIcon, MDBNavLink} from 'mdbreact';
import {BackTop} from 'antd';
import {Link} from 'react-router-dom';
import {toJS} from 'mobx';
import {inject, observer} from 'mobx-react';
import {Pagination} from 'antd'; //new
const numEachPage = 10;
class ArtworkFilter extends Component {
  constructor(props) {
    //new
    super(props);
    this.state = {
     
      artworks:[]
    };
  }



  render() {

    
    let {
      startingStore: {
        
        listOfEmergingArtist
        ,listOfUsers,listOfLatestArts
     
      },
    } = this.props;
 
    

    let a = listOfEmergingArtist.filter((data,i) =>{


      for(let x=0;x<this.props.filData.length;x++){
      if (
        data.artSize == this.props.filData[x] || data.artTheme == this.props.filData[x] 
     
        ){
        return data
        
        
      }
    
    } 
    })

    
    let filteredArtwork = listOfEmergingArtist.filter((art ) => Number(art.artPrice) >= Number(this.props.filPrice[0]) && Number(art.artPrice) <= Number(this.props.filPrice[this.props.filPrice.length-1]) ).map(art =>art)
 


    let artworks = () => {


if(this.props.filData[0] !== undefined || this.props.filPrice[0] !== undefined){


  return toJS(filteredArtwork.concat(a))
   
   .map((image,i)=>(
      <li key={i}>
               <Link
            to={{
              pathname: `/Art/${image.artworkID}/${image.artistName}`}} className='artlink'>
          <div className='artlabel'>
            <span className='new'>NEW</span>
            <span className='hot'>HOT</span>
            <span className='type'>
              {image.artType === 'Secondary'
                ? 'Second Edition'
                : 'Original'}
            </span>
          </div>
     
            <img src={image.artworkImg} alt='artwork' />
          </Link>
        
        <div className='artistinfo clearfix pad10'>
        
        <Link
            to={{
                    pathname: `/Art/${image.artworkID}/${image.artistName}`,
                    state: {artistName:image.artistName}
             
                }}
              >
                   <p className='artistname'>{image.artName}</p>
               
              </Link>

              
              <br/>
                 
              <Link
            to={{
                    pathname: `/Artist/${image.artistName}`,
                    state: {artistName:image.artistName}
               
                }}
              >
                   <p className='artistname' style={{fontSize:'10px',fontStyle:'italic'}}>by {image.artistName}</p>
               
              </Link>
      
      
          <MDBBtn
                className='ifollow'
                color='transparent'
                
                rounded
                title='Add To My Lists' 
              //  onClick={()=>{addtoList(image)}}
              >
              
                  <MDBIcon icon='plus' />
              
                </MDBBtn>
    </div>
      </li>
 ))
}else{
    return toJS(listOfEmergingArtist).map((image,i) =>  (
        <li key={i}>
                 <Link
              to={{
                pathname: `/Art/${image.artworkID}/${image.artistName}`}} className='artlink'>
            <div className='artlabel'>
              <span className='new'>NEW</span>
              <span className='hot'>HOT</span>
              <span className='type'>
                {image.artType === 'Secondary'
                  ? 'Second Edition'
                  : 'Original'}
              </span>
            </div>
       
              <img src={image.artworkImg} alt='artwork' />
            </Link>
          
          <div className='artistinfo clearfix pad10'>
          
          <Link
              to={{
                      pathname: `/Art/${image.artworkID}/${image.artistName}`,
                      state: {artistName:image.artistName}
               
                  }}
                >
                     <p className='artistname'>{image.artName}</p>
                 
                </Link>
  
                
                <br/>
                   
                <Link
              to={{
                      pathname: `/Artist/${image.artistName}`,
                      state: {artistName:image.artistName}
                 
                  }}
                >
                     <p className='artistname' style={{fontSize:'10px',fontStyle:'italic'}}>by {image.artistName}</p>
                 
                </Link>
        
        
            <MDBBtn
                  className='ifollow'
                  color='transparent'
                  
                  rounded
                  title='Add To My Lists' 
                //  onClick={()=>{addtoList(image)}}
                >
                
                    <MDBIcon icon='plus' />
                
                  </MDBBtn>
      </div>
        </li>
      )
      )
  }

  }
    return (
      <div>

          <ul className='col3img clearfix'>
            {artworks()}

          </ul>
      

    

        <div>
   
        </div>

        <div className='bttop clearfix'>
          <MDBNavLink to='/Upload' className='btnYellow'>
            Add Yours
          </MDBNavLink>
          <BackTop>
            {' '}
            <a href='#!'>
              Back to Top <MDBIcon icon='caret-right' />{' '}
            </a>{' '}
          </BackTop>
        </div>
      </div>
    );
  }
}

export default inject('startingStore')(observer(ArtworkFilter));
